
  import { mapActions } from 'vuex'
  
	export default {
		onLaunch() {
			console.log('App Launch')
      
      this.syncToken()
		},
		onShow() {
			console.log('App Show')
		},
		onHide() {
			console.log('App Hide')
		},
    methods: {
      ...mapActions(['checkUserToken']),
      
      async syncToken() {
        await this.checkUserToken()
      }
    }
	}
